import React from "react"

import styled from "styled-components"
import PropTypes from "prop-types"

const UserWrapper = styled.div`
  display: flex;
  align-items: center;
  margin: 0 auto 12px auto;
  &:last-child {
    margin-bottom: 0;
  }
`

const Avatar = styled.img`
  flex: 0 0 100px;
  width: 100px;
  height: 100px;
  margin: 0;
`

const Description = styled.div`
  flex: 1;
  margin-left: 18px;
  padding: 12px;
`
const Username = styled.h3`
  margin: 0 0 12px 0;
  padding: 0;
`

const Nickname = styled.span`
  font-size: 0.9rem;
  font-weight: bold;
  color: #999;
`

const Excerpt = styled.p`
  margin: 0;
`

const User = props => {
  return (
    <UserWrapper>
      <Avatar src={props.avatar} alt="" />
      <Description>
        <Username>
          {props.username} <Nickname>a.k.a {props.nickname}</Nickname>
        </Username>
        <Excerpt>{props.excerpt}</Excerpt>
      </Description>
    </UserWrapper>
  )
}

User.propTypes = {
  username: PropTypes.string,
  avatar: PropTypes.string,
  excerpt: PropTypes.string,
}

User.defaultProps = {
  username: ``,
  avatar: ``,
  excerpt: ``,
}

export default User
